.acceptModal {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 400;
  background-color: var(--backgroundLight);
}

.acceptModal--title {
  color: var(--red);
  font-size: 20px;
  font-weight: bold;
  padding: 20px;
  text-align: center;
}

.add {
  width: 100vw;
}

.add__buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: inherit;
}

.add__buttons * {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.addBoard {
  margin: 10px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: fit-content;
}

.addBoard__title {
  font-size: 15px;
  width: 100%;
  border-radius: 5px;
  border: unset;
  background-color: var(--backgroundDark);
  margin-top: 15px;
  margin-bottom: 5px;
  padding: 5px;
}

.addBoard__text {
  font-size: 15px;
  width: 100%;
  min-height: 200px;
  resize: none;
  border-radius: 5px;
  border: unset;
  background-color: var(--backgroundDark);
  margin-bottom: 5px;
  padding: 5px;
}

.addBoard__addPicture {
  font-size: 50px;
}

.addBoard__details {
  display: flex;
  flex-direction: row;
  min-height: fit-content;
}

.addBoard__details * {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 5px;
}

.addBoard__details input,
.addBoard__details select {
  font-size: 15px;
  resize: none;
  border-radius: 5px;
  border: unset;
  background-color: var(--backgroundDark);
  margin-bottom: 5px;
  padding: 5px;
  -webkit-appearance: none;
}

.addBoard__details label {
  font-size: 10px;
  font-weight: bold;
  margin-bottom: 0px;
}

.addBoard__details--price {
  border-radius: 5px;
  border: 1px solid black;
}

.addBoard__details--category {
  border-radius: 5px;
  border: 1px solid black;
}

.addBoard__input {
  display: none;
}

.addBoard__buttons {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}

.board {
  width: 95vw;
  max-width: 1000px;
  margin-top: 10px;
  font-size: 15px;
  border-radius: 0 0 5px 5px;
  border-bottom: 1px solid var(--red);
}

.board:last-child {
  margin-bottom: 20px;
}

.board__header {
  color: var(--special);
  background-color: var(--backgroundDark);
  border-radius: 5px 5px 0 0;
  text-align: center;
  padding: 5px;
  min-height: 50px;
  max-height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  font-size: 10px;
}

.board__header p {
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical !important;
  overflow: hidden;
  /* autoprefixer: off */
}

.board__header--title {
  font-size: 1.5em;
  text-align: center;
}

.board__header--details {
  font-size: 1em;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.board__content {
  transition: max-height 0.3s ease;
  max-height: 0px;
  overflow-y: auto;
  overflow-wrap: break-word;
  background-color: var(--backgroundDark);
  white-space: pre-line;
  align-self: normal;
}

.board__content img {
  width: 100%;
  overflow: hidden;
  object-fit: scale-down;
}

.board__content p {
  text-align: center;
  padding-left: 5px;
  padding-right: 5px;
}

.board__content--images {
  max-height: fit-content;
}

.board__content::-webkit-scrollbar {
  width: 5px;
  display: block;
}

.board__content::-webkit-scrollbar-thumb {
  background: var(--red);
  width: 1px;
}

.board__delete {
  margin-top: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  opacity: 0.8;
}

.board__delete:hover {
  opacity: 1;
}

.show {
  border-radius: 0 0 5px 5px;
  border-bottom: 3px solid var(--red);
}

.show .board__header {
  background-color: #e8c8ab;
}

.show .board__header div:last-child p {
  padding-top: 20px;
}

.show div {
  max-height: 500vh;
}

.show div p {
  -webkit-line-clamp: unset;
}

.remove {
  display: none;
}

.actionBtn {
  height: 40px;
  font-size: 17px;
  margin: 2px;
  border: unset;
  border-radius: 5px;
  opacity: 0.7;
  width: 95%;
  max-width: 500px;
  font-weight: bold;
  background-color: var(--special);
  color: var(--backgroundLight);
}

.actionBtn:active {
  opacity: 1;
}

.specialBtn {
  align-self: flex-end;
  margin: 20px;
  padding: 2px;
  padding-left: 25px;
  padding-right: 25px;
  font-size: 30px;
  font-weight: bold;
  color: var(--special);
}

.backButton {
  position: fixed;
  z-index: 501 !important;
  left: 0;
  border: unset;
}

.backButton * {
  font-size: 30px;
  color: var(--special);
}

.edit {
  background-color: royalblue;
}

.delete {
  background-color: red;
}

.buttonLink {
  width: 100%;
}

.cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
}

.cards__card {
  border: 1px solid var(--backgroundDark);
  padding: 0px;
  background-color: var(--backgroundDark);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
  width: 46vw;
  height: 60vw;
  max-width: 300px;
  margin: 5px;
  overflow: hidden;
  max-height: 400px;
}

.cards__card * {
  color: black;
}

.cards__card__title {
  font-size: 13px;
  font-weight: bold;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  padding: 5px;
  margin-bottom: auto;
  font-family: "PT Sans", sans-serif;
  /* autoprefixer: off */
}

.cards__card__price {
  font-size: 15px;
  font-weight: normal;
  margin: 5px;
  padding: 0;
  display: flex;
  justify-content: start;
  font-weight: 700;
  text-shadow: 0 0 chartreuse;
}

.cards__card__image {
  overflow: hidden;
  z-index: 2;
  object-fit: cover;
  height: inherit;
  width: 100%;
  padding: 0px;
}

.imageInput__label {
  width: 100%;
  font-size: 50px;
  text-align: center;
}

.imageInput--input {
  display: none;
}

.imageInput img {
  width: 50%;
  object-fit: contain;
}

:root {
  --backgroundLight: #fff1e5;
  --backgroundDark: #f2dfce;
  --special: #262a33;
  --red: #990f3d;
}

*::-webkit-scrollbar {
  display: none;
}

*::-webkit-scrollbar-thumb {
  background: #000;
}

a {
  text-decoration: none;
}

.error {
  color: red;
}

.error--icon {
  font-size: 50px;
}

.login {
  min-height: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

.login__info {
  font-weight: bold;
  text-align: center;
  color: var(--special);
}

.login__info div:last-child {
  font-size: 20px;
  margin: 10px;
}

.login__buttons {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__buttons input {
  font-size: 15px;
  border-radius: 5px;
  border: unset;
  background-color: var(--backgroundDark);
  padding: 5px;
  margin: 5px;
  -webkit-appearance: none;
}

.login__submit {
  width: 100%;
}

.productDetails__image {
  background-color: var(--backgroundDark);
  width: 100%;
  height: 200px;
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  overflow: scroll;
  object-fit: cover;
  z-index: 0;
}

.productDetails__image * {
  width: 100%;
  object-fit: contain;
  max-height: 200px;
}

.productDetails__image img {
  object-fit: contain;
  max-height: 200px;
  background-color: var(--backgroundDark);
}

.productDetails__image--price {
  width: 100%;
  display: flex;
  justify-content: center;
  font-weight: bold;
  color: var(--backgroundLight);
  background-color: var(--special);
  border-top: 0.1px solid var(--backgroundDark);
}

.productDetails__image--price * {
  margin: 2px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
}

.productDetails__details {
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: fit-content;
  background-color: var(--special);
}

.productDetails__details * {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-align: center;
  margin: 2px;
  width: 50%;
  color: var(--backgroundLight);
  /* autoprefixer: off */
}

.productDetails__price {
  color: var(--special);
  font-weight: bold;
  z-index: 5;
}

.productDetails__text {
  padding: 10px;
  white-space: pre-line;
  align-self: normal;
}

.productDetails__buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin-top: 60px;
  position: fixed;
  bottom: 50px;
  max-width: inherit;
}

.screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: calc(100vh - 90px);
  width: 100vw;
  background-color: var(--backgroundLight);
  margin-bottom: 45px;
  margin-top: 45px;
}

.screen--center {
  justify-content: center;
}

.screen__header {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--backgroundLight);
  font-weight: bold;
  color: var(--red);
  height: 45px;
  position: fixed;
  top: 0;
  z-index: 500;
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.56);
}

.screen__header * {
  padding: 10px;
  font-size: 18px;
  padding: 0;
  max-width: 80%;
  text-align: center;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.screen__container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: overlay;
  width: 100vw;
  max-width: 600px;
  min-height: inherit;
  position: relative;
}

.screen__buttons {
  z-index: 500;
  background-color: var(--backgroundLight);
  color: var(--text);
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.16);
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 45px;
  position: fixed;
  bottom: 0;
}

.screen__buttons * {
  font-size: 30px;
  background-color: var(--backgroundLight);
  border: unset;
}

.searchBox {
  height: 0px !important;
  border: none;
  padding: unset !important;
  padding-left: 5px !important;
  position: fixed;
  z-index: 502;
  max-width: 600px;
  -webkit-appearance: none;
  top: 5px;
  width: 95%;
  transition: height 0.5s;
  font-size: 18px;
  background-color: whitesmoke;
}

.searchBox:focus {
  height: 30px !important;
}

.icon {
  position: fixed;
  top: 0;
  z-index: 6;
  font-size: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.icon:active {
  color: var(--backgroundLight);
  background-color: var(--backgroundLight);
}

.icon--search {
  z-index: 501;
  right: 5px;
  padding-left: 50px;
}

.icon--category {
  z-index: 501;
  padding-right: 50px;
  left: 5px;
}

.imageSlider {
  overflow: hidden;
  z-index: 2;
  object-fit: cover;
  height: inherit;
  width: 100%;
  padding: 0px;
}

.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  opacity: 1;
  animation: spinner-opacity 2s ease;
}

.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: var(--backgroundDark);
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}

.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}

.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}

.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}

@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  100% {
    top: 24px;
    height: 32px;
  }
}

.spinner {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background-color: var(--backgroundLight);
  z-index: 499;
  display: flex;
  top: 0;
  align-items: center;
  justify-content: center;
  opacity: 1;
}

@keyframes spinner-opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

:root {
  --backgroundLight: #fff1e5;
  --backgroundDark: #f2dfce;
  --special: #262a33;
  --red: #990f3d;
}

.swiper-container {
  height: inherit !important;
}

.swiper-button-next {
  right: 0 !important;
  height: 100% !important;
  top: 20px !important;
  padding: 0 10px 0 30px !important;
  opacity: 0.5 !important;
  color: var(--special) !important;
  width: auto !important;
}

.swiper-button-prev {
  left: 0 !important;
  height: 100% !important;
  padding: 0 30px 0 10px !important;
  top: 20px !important;
  opacity: 0.5 !important;
  color: var(--special) !important;
  width: auto !important;
}

.swiper-pagination-bullets {
  bottom: 0px !important;
}

.swiper-pagination-bullet {
  width: 6px !important;
  height: 6px !important;
  border: 2px solid var(--backgroundLight);
}

.swiper-pagination-bullet-active {
  border: unset;
  width: 8px !important;
  height: 8px !important;
  background-color: var(--special) !important;
}

.swiper-button-disabled {
  opacity: 0.05 !important;
  pointer-events: visible !important;
}
